.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
    padding-top: 50px;
    @include md {
        padding-top: 36px; }
    @include sm {
        padding-top: 24px; }
    &__container {
        display: flex;
        align-items: center; }
    &__logo {
        margin-right: auto; } }


.nav {
    position: relative;
    @include sm {
        display: none; }
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include sm {
            flex-flow: column; } }
    &__item {
        color: #fff;
        margin-left: 50px; }
    &__link {
        font-size: 16px;
        font-weight: 300; }
    &__phone {
        font-size: 24px; }
    &.open {
        transform: translateY(0); } }


// ===== LOGO =====

.logo {
    display: block;
    &__text {
        display: inline-block;
        color: #fff;
        font-size: 67px;
        font-family: $corinthia;
        font-weight: 400;
        line-height: 1;
        vertical-align: middle;
        // +css-lock(40, 67, 767, 1400)
        @include md {
            font-size: 56px; }
        @include sm {
            font-size: 40px; } }
    &__img {
        display: inline-block;
        vertical-align: middle;
        margin-right: 30px;
        @include sm {
            margin-right: 12px; } } }

.logo--vert {
    text-align: center;
    .logo__text {
        display: {}
        color: #333333; }
    .logo__img {
        display: block;
        margin-right: 0;
        margin-bottom: 24px; } }

// ===== TOGGLE BUTTON =====
.toggle-button {
    position: relative;
    z-index: 20;
    width: 32px;
    height: 30px;
    display: none;
    @include sm {
        display: block; }
    span {
        display: block;
        width: 100%;
        height: 5px;
        background: white;
        margin: 4px auto;
        transition: all 0.3s;
        backface-visibility: hidden; } }

.toggle-button.on {
    span:nth-child(1) {
        transform: rotate(45deg) translate(5px, 5px); }
    span:nth-child(2) {
        opacity: 0; }
    span:nth-child(3) {
        transform: rotate(-45deg) translate(7px, -8px); } }




