.hero {
    height: 100vh;
    min-height: 630px;
    background-color: rgba($color: #2b2e26, $alpha: 0.5);
    &__video {
        height: 100%;
        width: 100%; }
    &__container {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        @include vertical; }
    &__text {
        display: inline-block;
        vertical-align: middle;
        color: #ffffff;
        text-align: center;
        max-width: 900px;
        min-width: 830px;
        padding: 150px 15px 150px;
        @include md {
            max-width: 800px;
            min-width: initial;
            padding: 150px 15px 150px; }
        @include sm {
            max-width: 740px; } }

    &__title {
        margin-bottom: 14px;
        font-size: 35px;
        line-height: 1;
        text-transform: uppercase;
        font-weight: 400;
        @include md {
            font-size: 28px; }
        @include sm {
            font-size: 24px; } }
    &__descr {
        margin-bottom: 48px;
        font-size: 25px;
        font-weight: 300;
        line-height: 1.32;
        @include md {
            font-size: 16px; } } }


